body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

.App {
  text-align: center;
  overflow-x:hidden;
}



.Title {
    box-sizing: border-box;
    width: 100%;
    display: flex;
    justify-content: center;
    align-items: baseline;
    padding: .5em;
    margin-bottom: 2em;
    background-color: rgb(192, 45, 26);
    color: #fff;
  }

  .Title-Subtitle {
    color: rgba(255, 255, 255, 0.75);
  }
  
  .Title .nav-link {
    display: inline-block;
    padding: 0.5em 1em;
    color: rgba(255, 255, 255, 0.835);
    font-weight: bold;
    text-decoration: none;
  }

  .Title .nav-link-active {
    color: #fff;
    border-bottom: 2px solid;
  }
  
button {
    padding: 0.5em;
    margin: 0;
    border: 1px solid;
    font-size: 0.5;
    border-radius: 0.5em;
  }
  .POPOSSpace {
    position: relative;
  }
  
  .POPOSSpace > img {
    width: 100%;
    height: auto;
  }

  .POPOSSpace > .POPOSSpace-info  > .Likes > img {
    width: 1rem;
    height: 1rem;
    margin-left: .5rem;
  }


  .POPOSSpace  .POPOSSpace-title {
    display: block;
    position: absolute;
    left: 0;
    top: 0;
    text-decoration: none; 
    color: black;
  } 

  .POPOSSpace h1 {
    margin: 0;
    font-weight: lighter;
    background-color: rgba(255, 255, 255, 0.9);
    padding: 0.5rem;
    text-align: left;
  }

  .POPOSSpace .POPOSSpace-info {
    position: absolute;
    right: 0;
    bottom: 0;
    text-align: right;
  }

  .POPOSSpace .POPOSSpace-info div {
    display: inline-block;
    padding: 0.25em 0.5em;
    background-color: rgba(255, 255, 255, 0.9);
  } 
.POPOSList {
    display: grid;
    grid-template-columns: 1fr 1fr 1fr;
    grid-gap: 2em;
    max-width: 1200px;
    margin: auto;
    padding: 2em;
  }

  @media only screen and (max-width: 960px) {
    .POPOSList {
        grid-template-columns: 1fr 1fr;
    }
  }
  
  @media only screen and (max-width: 700px) {
    .POPOSList {
        grid-template-columns: 1fr;
    }
  }

  div form {
    align-items: center;
  }

  div form input,  form button {
    padding: 0.5em;
    margin: 0;
    border: 1px solid;
    font-size: 1;
  }

  div form input {
    border-top-left-radius: 0.5em;
    border-bottom-left-radius: 0.5em;
    
  }

  div form button {
    border-top-right-radius: 0.5em;
    border-bottom-right-radius: 0.5em;
  }
.About> p{
    margin: 2em;
    width: 40rem;
    text-align: left;
    padding-left: 6em;
}
.Footer{
    left: 0;
    bottom: 0;
    width: 100%;
    text-align: center;
    color: #fff;
    padding: 1rem;
    background-color: rgb(192, 45, 26);
    margin-top: .5em;
}


.POPOSDetails {
    display: flex;
    flex-direction: row-reverse;
    width: 960px;
    margin: auto;
}

.POPOSDetails .POPOSDetails-info {
    flex: 1 1;
    text-align: left;
    padding: 0 1em;
}

.POPOSDetails .POPOSDetails-title {
    font-size: 2em;
    margin: 0;
}

.POPOSDetails .POPOSDetails-image {
    flex: 2 1;
}

.POPOSDetails .POPOSDetails-image img {
    width: 100%;
}

.POPOSDetails .POPOSDetails-hours {
    font-size: 1.5em;
}
.POPOSFeature {
    font-size: 2em;
    padding: 0.25em;
    margin: 0.125em;
    border-radius: 0.25em;
    background-color: #eee;
}
.POPOSFeatureList {
    display: flex;
}
