  .POPOSSpace {
    position: relative;
  }
  
  .POPOSSpace > img {
    width: 100%;
    height: auto;
  }

  .POPOSSpace > .POPOSSpace-info  > .Likes > img {
    width: 1rem;
    height: 1rem;
    margin-left: .5rem;
  }


  .POPOSSpace  .POPOSSpace-title {
    display: block;
    position: absolute;
    left: 0;
    top: 0;
    text-decoration: none; 
    color: black;
  } 

  .POPOSSpace h1 {
    margin: 0;
    font-weight: lighter;
    background-color: rgba(255, 255, 255, 0.9);
    padding: 0.5rem;
    text-align: left;
  }

  .POPOSSpace .POPOSSpace-info {
    position: absolute;
    right: 0;
    bottom: 0;
    text-align: right;
  }

  .POPOSSpace .POPOSSpace-info div {
    display: inline-block;
    padding: 0.25em 0.5em;
    background-color: rgba(255, 255, 255, 0.9);
  } 